.home__container {
    row-gap: 7rem;
}

.home__content {
    grid-template-columns: 116px repeat(2, 1fr);
    padding-top: 5.5rem;
    column-gap: 2rem;
    align-items: center;
}

.home__social {
    display: grid;
    grid-template-columns: max-content;
    row-gap: 1rem;
}

.home__social-icon {
    font-size: 1.25rem;
    color: var(--title-color);
}

.home__social-icon:hover {
    color: var(--title-color-dark);
}

.home__title {
    font-size: var(--big-font-size);
    margin-bottom: var(--mb-0-25);
}

.home__hand {
    width: 38px;
    height: 38px;
    margin-left: 0.4rem;
}

.home__subtitle {
    position: relative;
    font-size: var(--h3-font-size);
    padding-left: 5.4rem;
    font-weight: var(--font-normal);
    margin-bottom: var(--mb-1);
}

.home__subtitle::before {
    content: '';
    position: absolute;
    width: 70px;
    height: 1px;
    background-color: var(--text-color);
    left: 0;
    top: 1rem;
}

.home__description {
    max-width: 400px;
    margin-bottom: var(--mb-3);
}

.home__img {
    /* background-image: url(../../assets//profile.jpg); */
    background-image: url(../../assets//avt.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    /* box-shadow: inset 0 0 0 9px rgb(255, 255, 255 / 30%); #để ntn thì không có viền trông cũng vui */
    box-shadow: inset 0 0 0 9px rgb(255 255 255 / 30%); /* kiểu này thì có viền (sau này làm js thì thêm toggle có viền hoặc không) */
    /*
     => box-shadow: inset 0 0 0 9px rgb(255, 255, 255 / 30%);

        Chia cho 30% ở đây được sử dụng để xác định giá trị alpha (độ mờ) của màu trắng được chỉ định. 
        Giá trị alpha phải được chỉ định dưới dạng số thập phân từ 0 đến 1. 
        Chia cho 30% tương đương với việc nhân với 0,3, 
        do đó giá trị alpha sẽ là 0,3 trong trường hợp này. 
     */
    order: 1; /* đổi vị trí cho  .home__content */
    justify-self: center;
    width: 300px;
    height: 300px;

    /* Cho ảnh méo luôn từ đẩu để không bị khựng khi từ vuông sang méo */
    border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;

    /* animation */
    animation: profile__animate 8s ease-in-out infinite 1s;

    /* 
     => animation: profile__animate 8s ease-in-out infinite 1s;
    profile__animate: tên của keyframes mà sẽ được sử dụng để kích hoạt hoạt ảnh.
    8s: thời gian mà hoạt ảnh sẽ diễn ra, ở đây là 8 giây.
    ease-in-out: kiểu easing được áp dụng cho hoạt ảnh, ở đây là ease-in-out, có nghĩa là hoạt ảnh sẽ bắt đầu chậm, tăng tốc giữa chừng, rồi chậm lại trước khi kết thúc.
    infinite: sẽ kích hoạt hoạt ảnh vô hạn lần, nghĩa là nó sẽ tiếp tục chạy sau khi kết thúc.
    1s: thời gian trễ giữa mỗi lần chạy hoạt ảnh sau khi kết thúc. Ở đây là 1 giây.
    */
    cursor: pointer;
}

@keyframes profile__animate {
    0% {
        border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
    }

    50% {
        border-radius: 30% 60% 70% 40% / 50% 60% 30% 60%;
    }

    100% {
        border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
    }
}

/* home scroll down */
.home__scroll {
    margin-left: 9.25rem;
}

.wheel {
    animation: scroll 2s ease infinite;
}

@keyframes scroll {
    0% {
        transform: translateY(0);
    }

    30% {
        transform: translateY(3.75rem);
    }
}

.home__scroll-name {
    color: var(--title-color);
    font-weight: var(--font-medium);
    margin-right: var(--mb-0-25);
    margin-left: 2px;
}

.home__scroll-arrow {
    font-size: 1.25rem;
    color: var(--title-color);
}

/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
    .home__content {
        grid-template-columns: 100px repeat(2, 1fr);
        column-gap: 1.25rem;
    }

    .home__hand {
        width: 26px;
        height: 26px;
    }

    .home__subtitle {
        padding-left: 3.75rem;
        margin-bottom: var(--mb-1);
    }

    .home__subtitle::before {
        width: 42px;
        top: 0.8rem;
    }

    .home__description {
        max-width: initial;
        margin-bottom: var(--mb-2-5);
    }

    .home__img {
        width: 250px;
        height: 250px;
        box-shadow: inset 0 0 0 8px rgb(255 255 255 / 30%);
    }

    .home__scroll {
        margin-left: 7.5rem;
    }
}

/* For medium devices */
@media screen and (max-width: 768px) {
    .home__content {
        grid-template-columns: 0.5fr 3fr;
        padding-top: 3.5rem;
    }

    .home__img {
        order: initial;
        justify-self: initial;
    }

    .home__data {
        grid-column: 1/3;
    }

    .home__img {
        box-shadow: inset 0 0 0 6px rgb(255 255 255 / 30%);
        width: 200px;
        height: 200px;
    }

    .home__scroll {
        display: none;
    }
}

/* For small devices */
@media screen and (max-width: 350px) {
    .home__img {
        height: 180px;
        width: 180px;
    }

    .home__hand {
        width: 22px;
        height: 22px;
    }
}
